import ClientOAuth2 from 'client-oauth2';
import { useEffect, useCallback, useMemo } from 'react';
import { useLocation } from 'react-router-dom';
import { useRecoilState, useSetRecoilState } from 'recoil';
import { useAuth } from '../../../auth/Authcontext';

import { filterFavoritesState } from '../../../state/recoil/repositories';
import { userTokenState } from '../../../state/recoil/token';

const APP_KEY = 'd2dc6a9a2734e459f2b4';
const APP_SECRET = 'e67e5dc3252cf79c3bb2b883dc8edcadc3fb2c98';

var githubAuth = new ClientOAuth2({
  clientId: APP_KEY,
  clientSecret: APP_SECRET,
  accessTokenUri: 'https://github.com/login/oauth/access_token',
  authorizationUri: 'https://github.com/login/oauth/authorize',
  redirectUri: 'localhost:3000',
  scopes: ['user', 'user:email'],
});

declare const window: any;

window.oauth2Callback = function (uri: string) {
  githubAuth.token.getToken(uri).then(function (user) {
    alert(user.accessToken);
    console.log(user);
  });
};

export default function useToken() {
  const setFilterFavorites = useSetRecoilState(filterFavoritesState);

  const { user, setGitHubToken } = useAuth();

  const [token, setToken] = useRecoilState(userTokenState);

  useEffect(() => {
    user?.ghtoken && setToken(user.ghtoken);
  }, [setToken, user?.ghtoken]);

  useEffect(() => {
    setFilterFavorites(false);
  }, [token, setFilterFavorites]);

  const requestToken = useCallback(() => {
    // Open the page in a new window, then redirect back to a page that calls our global `oauth2Callback` function.
    window.location = githubAuth.token.getUri();
  }, []);

  return { token, requestToken };
}
